<template>
    <tr>
        <td @keyup.13="onblur_" >
            <input type="text" class="form-control" name="type" v-model="equipment_type">
        </td>
        <td @keyup.13="onblur_" >
            <input type="text" class="form-control" name="type" v-model="manufacturer">
        </td>
        <td @keyup.13="onblur_" >
            <input type="text" class="form-control" name="type" v-model="name">
        </td>
        <td @keyup.13="onblur_" >
            <input type="text" class="form-control" name="type" v-model="price">
        </td>
        <td @keyup.13="onblur_" >
            <input type="text" class="form-control" name="type" v-model="owner">
        </td>
        <td @keyup.13="onblur_" >
            <input type="text" class="form-control" name="type" v-model="belonging">
        </td>
        <td @keyup.13="onblur_" >
            <input type="text" class="form-control" name="type" v-model="number">
        </td>
        <td @keyup.13="onblur_" >
            <input type="date" class="form-control" name="type" v-model="date_in">
        </td>
        <td @keyup.13="onblur_" >
            <input type="date" class="form-control" name="type" v-model="date_out">
        </td>
    </tr>
</template>

<script>
import {
  getAPI
} from '../api/axios-base'
import {
  mapState
} from 'vuex'
export default {
  name: 'AddForm',
  components: {
  },
  data () {
    return {
        equipment_type: '',
        manufacturer: '',
        name: '',
        price: '',
        owner: '',
        belonging: '',
        number: '',
        date_in: '',
        date_out: ''
    }
  },
  props: {
    relatedItem: Number
  },
  methods: {
    onblur_ (event) {
        let data = {
            equipment_type: this.equipment_type,
            manufacturer: this.manufacturer,
            name: this.name,
            price: this.price,
            owner: this.owner,
            belonging: this.belonging,
            number: this.number,
            date_in: this.date_in,
            date_out: this.date_out
        }
        if (this.relatedItem !== 0) {
            data.relatedItem = this.relatedItem
        }
          getAPI.post('add', data).then(response => {
              if (response.data.status === 'ok') {
                this.equipment_type = ''
                this.manufacturer = ''
                this.name = ''
                this.price = ''
                this.owner = ''
                this.belonging = ''
                this.number = ''
                this.date_in = ''
                this.date_out = ''
                this.relatedItem = 0
                  getAPI.get('list', {
                      headers: {
                          Authorization: `Bearer ${this.$store.state.accessToken}`
                      }
                  })
                      .then(response => {
                          console.log('GetAPI successfully got the mods')
                          this.$store.state.elements = response.data.elements
                      })
              } else {
                alert(response.data.status)
              }
          })
    }
  },
  computed: mapState(['elements', 'isEditable']),
  created () {
  }
}
</script>
