<script>
import {
  getAPI
} from '../api/axios-base'
import {
  mapState
} from 'vuex'
export default {
  name: 'ExcelExporter',
  components: {
  },
  data () {
    return {
        leftDate: Date(),
        rightDate: Date()
    }
  },
  methods: {
    getExport (event) {
        console.log(this.leftDate)
        console.log(this.rightDate)
        let data = {
            left_date: this.leftDate,
            right_date: this.rightDate
        }
        getAPI.post('range', data).then(response => {
            console.log(response)
            window.open(response.data.url, '_blank').focus()
        })
    }
  },
  computed: mapState(['elements', 'isEditable']),
  created () {
  }
}
</script>
