<template>
  <div>
    <div class="d-flex flex-row   pb-2 pt-2">
      <div class="col-md-3"></div>
      <div class="col-md-6 pr-2">
        <input id="filter" type="text" class="form-control" placeholder="Поиск" v-model="search">
      </div>
      <div class="col-md-1 space-after-search"></div>
      <div class="col-md-1 review-div">
        <button  type="button" class="btn btn-light nav-btn" data-toggle="modal" data-target="#exampleModal">
            Отчет
        </button>
      </div>
      <div class="col-md-1 add-item-div">
        <button @click="showAddForm = !showAddForm" class="btn btn-light nav-btn plus-btn">Внести</button>
      </div>
    </div>
    <table class="table table-bordered table-hover">
      <thead>
        <tr>
          <th scope="col" data-param-name="equipment_type">Тип</th>
          <th scope="col" data-param-name="manufacturer" @click="sort_col">Производитель</th>
          <th scope="col" data-param-name="name" @click="sort_col">Наименование оборудования</th>
          <th scope="col" data-param-name="price" @click="sort_col">Стоимость оборудования</th>
          <th scope="col" data-param-name="owner" @click="sort_col">Принадлежность оборудования</th>
          <th scope="col" data-param-name="belonging" @click="sort_col">Закрепление оборудования за сотрудниками</th>
          <th scope="col" data-param-name="number" @click="sort_col">Накладной номер оборудования</th>
          <th scope="col" data-param-name="date_in" @click="sort_col">Дата получения оборудования</th>
          <th scope="col" data-param-name="date_out" @click="sort_col">Дата списания/выдачи оборудования из ремонта</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="element in searchResults" :key=element.id :id="element.id" @click="checkChildrens(element.id)">
          <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="equipment_type">
            {{ element.equipment_type }}</td>
          <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="manufacturer">{{
            element.manufacturer
          }}
          </td>
          <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="name">{{ element.name }}</td>
          <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="price">{{ element.price }}</td>
          <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="owner">{{ element.owner }}</td>
          <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="belonging">{{ element.belonging }}</td>
          <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="number">{{ element.number }}</td>
          <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="date_in">{{ element.date_in }}</td>
          <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="date_out">{{ element.date_out }}</td>
          <td>
            <div class="btn-group dropleft">
              <button type="button" class="btn btn-secondary dropdown-toggle dropdown-toggle-split" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              </button>
              <div class="dropdown-menu">
                <a class="dropdown-item" @click="addRelatedItem(element.id)">Добавить оборудование</a>
                <a class="dropdown-item" @click="remove(element.id)">Удалить</a>
              </div>
            </div>
          </td>
        </tr>
        <AddForm v-if="showAddForm" :relatedItem="relatedItem" />
      </tbody>
    </table>
    <div class="related-items-contaiter">
    <RelatedItemsTable v-if="showRelatedItems" :elements="relatedItems" :relatedId="relatedItem" @close="closeRelatedTable"/>
    </div>
  </div>
</template>

<script>
import {
  getAPI
} from '../api/axios-base'
import {
  mapState
} from 'vuex'
import AddForm from '../components/AddItem.vue'
import RelatedItemsTable from '../components/RelatedItems.vue'

export default {
  name: 'Elements',
  components: {
    AddForm,
    RelatedItemsTable
  },
  data () {
    return {
      sort_by: '',
      sort: '',
      search: '',
      showAddForm: false,
      relatedItem: 0,
      relatedItems: {},
      showRelatedItems: false
    }
  },
  methods: {
    closeRelatedTable () {
      this.showRelatedItems = !this.showRelatedItems
      if (document.getElementsByClassName('table-active').length > 0) {
        document.getElementsByClassName('table-active')[0].classList.toggle('table-active')
      }
    },
    checkChildrens (id) {
      if (document.getElementsByClassName('table-active').length > 0) {
        document.getElementsByClassName('table-active')[0].classList.toggle('table-active')
      }
      event.target.parentElement.classList.toggle('table-active')
      getAPI.post('related', { id: id }).then(response => {
        console.log('Got related items')
        if (Object.keys(response.data.elements).length > 0) {
          this.showRelatedItems = true
          this.relatedItems = response.data.elements
          this.relatedItem = id
        } else {
          this.showRelatedItems = false
          this.relatedItems = {}
        }
      })
    },
    addRelatedItem (id) {
      this.relatedItem = id
      this.showAddForm = true
    },
    async sort_col (event) {
      let paramName = event.target.dataset.paramName
      if (this.sort === '' || this.sort === 'ASC') {
        this.sort = 'DESC'
      } else {
        this.sort = 'ASC'
      }
      this.sort_by = paramName

      getAPI.post('sorted', { attribute: paramName, sort: this.sort }).then(response => {
        console.log('GetAPI successfully got the mods')
        this.$store.state.elements = response.data.elements
      })
        .catch(err => {
          console.log(err)
        })
    },
    onclick_ (event) {
      if (this.isEditable) {
        event.target.setAttribute('contenteditable', 'true')
      }
    },
    onblur_ (event) {
      if (this.isEditable) {
        event.preventDefault()
        event.target.innerHTML = event.target.innerHTML.replaceAll('<br>', '')
        event.target.setAttribute('contenteditable', 'false')
        let elementId = event.target.parentElement.id
        let paramName = event.target.dataset.paramName
        let newValue = event.target.innerHTML
        console.log(newValue)
        this.elements[Number(elementId)][paramName] = newValue
        getAPI.post('save', { element_id: elementId, attribute: paramName, value: newValue })
      }
    },
    onInput (e) {
      console.log(e)
    },
    async remove (id) {
      await getAPI.post('remove', { element_id: id }).then(response => {
        console.log('removed element')
      })
      getAPI.get('list', {
      headers: {
        Authorization: `Bearer ${this.$store.state.accessToken}`
      }
    })
      .then(response => {
        console.log('GetAPI successfully got the mods')
        this.$store.state.elements = response.data.elements
      })
    }
  },
  computed: {
    searchResults () {
      if (this.search === '') {
        return this.elements
      } else {
        let results = {}
        Object.entries(this.elements).forEach(([id, element]) => {
          Object.entries(element).forEach(([key, value]) => {
            if (key !== 'id') {
              if (String(value).indexOf(this.search) !== -1) {
                results[id] = element
              }
            }
          })
        })
        console.log(results)
        return results
      }
    },
    ...mapState(['elements', 'isEditable'])
  },
  created () {
    this.addingItem = false
    console.log(this.relatedItems)
    getAPI.get('list', {
      headers: {
        Authorization: `Bearer ${this.$store.state.accessToken}`
      }
    })
      .then(response => {
        console.log('GetAPI successfully got the mods')
        this.$store.state.elements = response.data.elements
      })
      .catch(err => {
        console.log(err)
      })

    getAPI.get('rights', {
      headers: {
        Authorization: `Bearer ${this.$store.state.accessToken}`
      }
    })
      .then(response => {
        console.log('GetAPI successfully got the mods')
        this.$store.state.isEditable = response.data.edit_group
        console.log(this.$store.state.isEditable)
      })
      .catch(err => {
        console.log(err)
      })
  }
}
</script>

<style>
.review-div{
  padding-right: 0px !important;
  padding-left: 0px !important;
  width: 90% !important;
}
.add-item-div{
  padding-left: 0px !important;
  padding-right: 20px !important;
}
.space-after-search{
  padding-right: 0px !important;

}
.close-btn-div{
  padding-top: 0.5%;
  padding-right: 0.5%;
}
