<template>
    <div>
        <p>Связанные элементы <button type="button" class="btn btn-secondary btn-sm" @click="closeTable">X</button></p>
      <table class="table table-bordered table-hover">
        <thead>
          <tr>
            <th scope="col" data-param-name="equipment_type">Тип</th>
            <th scope="col" data-param-name="manufacturer" @click="sort_col">Производитель</th>
            <th scope="col" data-param-name="name" @click="sort_col">Наименование оборудования</th>
            <th scope="col" data-param-name="price" @click="sort_col">Стоимость оборудования</th>
            <th scope="col" data-param-name="owner" @click="sort_col">Принадлежность оборудования</th>
            <th scope="col" data-param-name="belonging" @click="sort_col">Закрепление оборудования за сотрудниками</th>
            <th scope="col" data-param-name="number" @click="sort_col">Накладной номер оборудования</th>
            <th scope="col" data-param-name="date_in" @click="sort_col">Дата получения оборудования</th>
            <th scope="col" data-param-name="date_out" @click="sort_col">Дата списания/выдачи оборудования из ремонта</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="element in elements" :key=element.id :id="element.id">
            <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="equipment_type">
              {{ element.equipment_type }}</td>
            <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="manufacturer">{{
              element.manufacturer
            }}
            </td>
            <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="name">{{ element.name }}</td>
            <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="price">{{ element.price }}</td>
            <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="owner">{{ element.owner }}</td>
            <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="belonging">{{ element.belonging }}</td>
            <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="number">{{ element.number }}</td>
            <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="date_in">{{ element.date_in }}</td>
            <td @dblclick="onclick_" @keyup.13="onblur_" @input="onInput" data-param-name="date_out">{{ element.date_out }}</td>
            <td>
            <div class="btn-group dropleft">
              <button type="button" class="btn btn-secondary dropdown-toggle dropdown-toggle-split" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                <span class="sr-only">Toggle Dropright</span>
              </button>
              <div class="dropdown-menu">
                <a class="dropdown-item" @click="remove(element.id)">Удалить</a>
              </div>
            </div>
          </td>
          </tr>
        </tbody>
      </table>
    </div>
  </template>
  <script>
  import {
    getAPI
  } from '../api/axios-base'
  import {
  mapState
} from 'vuex'
  export default {
    name: 'RelatedItemsTable',
    props: {
      elements: Object,
      relatedId: Number
    },
    data () {
      return {
        sort_by: '',
        sort: ''
      }
    },
    computed: mapState(['isEditable']),
    methods: {
      closeTable () {
        this.$emit('close')
      },
      async sort_col (event) {
        let paramName = event.target.dataset.paramName
        if (this.sort === '' || this.sort === 'ASC') {
          this.sort = 'DESC'
        } else {
          this.sort = 'ASC'
        }
        this.sort_by = paramName
        getAPI.post('sorted_rel', { attribute: paramName, sort: this.sort, id: this.relatedId }).then(response => {
          console.log('GetAPI successfully got the mods')
          this.elements = response.data.elements
        })
          .catch(err => {
            console.log(err)
          })
      },
      onclick_ (event) {
        if (this.isEditable) {
          event.target.setAttribute('contenteditable', 'true')
        }
      },
      onblur_ (event) {
        if (this.isEditable) {
          event.preventDefault()
          event.target.innerHTML = event.target.innerHTML.replaceAll('<br>', '')
          event.target.setAttribute('contenteditable', 'false')
          let elementId = event.target.parentElement.id
          let paramName = event.target.dataset.paramName
          let newValue = event.target.innerHTML
          console.log(newValue)
          this.elements[Number(elementId)][paramName] = newValue
          getAPI.post('save', { element_id: elementId, attribute: paramName, value: newValue })
        }
      },
      onInput (e) {
        console.log(e)
      },
      async remove (id) {
        console.log(id)
      await getAPI.post('remove', { element_id: id }).then(response => {
        console.log('removed element')
      })
      getAPI.post('related', { id: this.relatedId }).then(response => {
        console.log('Got related items')
        this.elements = response.data.elements
      })
    }
    },
    created () {
        console.log(this.elements)
    }
  }
  </script>
